// @flow
import * as React from 'react';
import { Survey, Model } from 'survey-react';
import 'survey-react/modern.css';
import _isEmpty from 'lodash.isempty';
import _get from 'lodash.get';
import type { SurveyModel } from 'survey-react';
import { useTheme, useMediaQuery } from '@material-ui/core';
import {
	RadioGroup,
	Checkbox,
	DatetimePicker,
	Dropdown,
	SingleInput,
	Textarea,
	MultipleText,
	MatrixSingle,
	MatrixMultiple,
	MatrixDynamic,
	Boolean,
	PhoneInput,
	Panel,
	Expression,
	FileUpload,
	PanelDynamic,
} from './Elements';
import './Styles.css';
import './Elements/Step/Style.css';
import './Elements/Step/Button.css';
import { onValidateHandler, getValidUploadFiles } from './Validators';
import { ELEMENTS_TYPE, SURVEYJS_MODE } from './Constants';
import { SURVEYS_ERROR_MESSAGE } from './Messages';
import './setting';
import {
	isFormInDisplayMode,
	applyFormProperty,
	switchIdToName,
	isFormCounterOffer,
} from '../../../../helpers';
import { useSnackBar } from '../../../../hooks';

type Props = {
	formTemplate: string;
	onCompleteHandler?: Function | undefined;
	onSaveExitHandler?: Function | undefined;
	onPageChangedHandler?: Function | undefined;
	onFilesUploadHandler?: Function | undefined;
	formData: string;
	mode: string;
	formProperty: string;
	onAfterRenderSurveyHandler?: Function;
	formType?: string;
};
const surveyModel = (template: any, mode: string, isMobile: boolean) => {
	const survey = new Model(template);

	survey.completeText = 'Submit';

	survey.onUpdateQuestionCssClasses.add((survey: SurveyModel, options: any) => {
		[
			RadioGroup,
			Checkbox,
			DatetimePicker,
			Dropdown,
			SingleInput,
			Textarea,
			MultipleText,
			MatrixSingle,
			MatrixMultiple,
			MatrixDynamic,
			Boolean,
			PhoneInput,
			Expression,
			FileUpload,
			PanelDynamic,
		].map(control => control(survey, options, isMobile));
	});

	survey.onUpdatePanelCssClasses.add((senders: SurveyModel, options: any) => {
		Panel(options);
	});

	survey.onAfterRenderQuestion.add((sender: SurveyModel, options: any) => {
		const question = _get(options, 'question');
		if (question.getType() === ELEMENTS_TYPE.file) {
			question.storeDataAsText = false;
			question.allowImagesPreview = false;
		}
	});
	if (isFormInDisplayMode(mode)) survey.mode = SURVEYJS_MODE.display;

	return survey;
};

const Engine = (props: Props) => {
	const {
		formTemplate,
		onCompleteHandler,
		onSaveExitHandler,
		onPageChangedHandler,
		formData,
		onFilesUploadHandler,
		mode,
		formProperty,
		onAfterRenderSurveyHandler,
		formType,
	} = props;

	const theme = useTheme();
	const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
	const showSnackBar = useSnackBar();
	const surveyTemplate = JSON.parse(formTemplate);
	const survey = surveyModel(surveyTemplate, mode, isMobile);

	const removeSaveExitButton = () => {
		const saveExitButton = document.getElementById('save-exit-btn');
		if (saveExitButton) saveExitButton.remove();
	};

	const addSaveExitButton = () => {
		const id = 'save-exit-btn';
		let saveExitButton = document.getElementById(id);
		if (saveExitButton === null) {
			saveExitButton = document.createElement('input');
			saveExitButton.className = ` sv-btn sv-footer__${id}`;
			// @ts-ignore
			saveExitButton.value = 'Save & Exit';
			// @ts-ignore
			saveExitButton.type = 'button';
			saveExitButton.id = id;
			saveExitButton.addEventListener('click', () => {
				if (onSaveExitHandler) {
					onSaveExitHandler(survey, surveyTemplate);
				}
			});
			// @ts-ignore
			const [footer] = document.getElementsByClassName('sv-body__footer');

			if (isMobile) {
				saveExitButton.style.width = '98%';
				footer.style.display = 'block';
				saveExitButton.style.marginBottom = '10px';
			}
			footer.insertBefore(saveExitButton, footer.firstChild);
		}
	};

	const fetchSavedData = () => {
		const data = switchIdToName(survey, JSON.parse(formData), surveyTemplate);
		survey.data = data;
	};

	survey.onUploadFiles.add((survey: SurveyModel, options: any) => {
		const validFiles = getValidUploadFiles(options);
		if (!_isEmpty(validFiles)) {
			const newOptions = { ...options, files: validFiles };
			if (onFilesUploadHandler) {
				onFilesUploadHandler({ survey, options: newOptions });
			}
		}
	});

	survey.onComplete.add((survey: SurveyModel) => {
		if (!isFormInDisplayMode(mode) && onCompleteHandler)
			onCompleteHandler(survey, surveyTemplate);
	});

	survey.onCurrentPageChanged.add((survey: SurveyModel, options: any) => {
		if (
			_get(options, 'isNextPage') &&
			!isFormInDisplayMode(mode) &&
			onPageChangedHandler
		) {
			onPageChangedHandler(survey, surveyTemplate);
		}
	});

	if (onAfterRenderSurveyHandler) {
		survey.onAfterRenderSurvey.add(() => {
			onAfterRenderSurveyHandler();
		});
	}

	const { submitOnConfirmation, confirmationMessage = '' } = surveyTemplate;
	submitOnConfirmation &&
		confirmationMessage &&
		survey.onCompleting.add((sender: SurveyModel, options) => {
			// eslint-disable-next-line no-param-reassign,no-alert
			options.allowComplete = window.confirm(confirmationMessage);
		});

	// filtering to get a list of type file questions
	if (!_isEmpty(formProperty)) {
		survey.getAllQuestions().forEach(question => {
			// @ts-ignore
			applyFormProperty(question, JSON.parse(formProperty));
		});
	}

	React.useEffect(() => {
		if (!isFormCounterOffer(formType)) {
			addSaveExitButton();
		}
	}, []);

	React.useEffect(() => {
		if (isFormInDisplayMode(mode)) removeSaveExitButton();
	}, [mode]);

	React.useEffect(() => {
		if (_isEmpty(formData)) return;
		try {
			fetchSavedData();
		} catch (e) {
			const message = _get(e, 'message') || SURVEYS_ERROR_MESSAGE.COMMON;
			showSnackBar({
				message,
			});
		}
	}, [formData]);

	return <Survey model={survey} onValidateQuestion={onValidateHandler} />;
};

export default Engine;
